*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Condensed', sans-serif;
   
}
body{
  background-color:rgb(247, 238, 223);
}
.head-div{

    background-color: rgb(230, 74, 12);
    color: white;
    padding: 10px 0;
    width: 100vw;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.time-head{
    text-align: center;
    margin: 4px 10px;
    padding: 6px;
}
.header{
    text-align: center;
}
.clone{
    margin: 0 10px;
}

.content{
    margin: 8px 0;
    width: 100vw;
    height: 24vh;
    
}
.info-content{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width:80%;
    height: 140px;
    margin: auto;
    padding:6px;
    border-radius: 40px;
    box-shadow: 1px 1px 4px red;
    background-color: lightgrey;

    
}
.input{
    width: 100%;
    margin-top: 18px;
    border-radius: 40px;
    text-align: center;
    padding: 3px 0;
    font-weight:bold ;
    font-size:1.1rem;
}
.textarea{
    width: 100%;
    height: auto;
    margin: 2px auto;
    border-radius: 40px;
    text-align: center;
    font-weight: bold;
    font-size:1.1rem;
}
.btn{
    border-radius: 40px;
    cursor: pointer;
    padding: 4px;
}
.btn-secondary{
    width: 8vw;
    margin: auto;

}
.main{
  display: flex;
  flex-wrap: wrap;
  background-color: lightpink;
  width: 100vw;
  height: auto;
  margin-top: 16px;
    
}
.details{
    width:30vw;
    height: auto;
    box-shadow: 1px 1px 4px red;
    text-align: center;
    padding: 3px 4px;
    margin: 4px;
    margin-top: 20px;
    border-radius: 40px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: bisque;
}
.detail-head{
    margin-bottom:4px ;
}
.detail-content{
    margin-bottom: 4px;
}
.footer{
    background-color: lightblue;
    position: sticky;
    bottom: 0;
    width: 100vw;
    height: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 8px 0;
}
.footer-head{
    text-align: center;

}
@media(max-width:300) {
    .details{
        width:70vw;
        padding:15px 10px;
      
    }
    .detail-head{
        text-align: center;
        font-size: 0.5rem;
    }
    .detail-content{
        font-size: 0.4rem;
    }
    
}